body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.jumbotron {
  background-color: #e9c8c1 !important;
}

.second-button {
  background-color: #dbdbdd !important;
}


.about-me {
  background-color: #f9f9f9;
}

.about-me-paragraph {
  font-size: 19px;
}

.empty {
  margin-top: px;
}

.more-button {
  background-color: #e9c8c1 !important;
  color: white !important;
}

.empty {
  margin-bottom: 120px;
}

.opinion {
  background-color: #e9c8c1 !important;
  color: white; }

.opinie {
  background-color: #e9c8c1 !important;
  opacity: 0.5; }

.navigation {
  color: #e9c8c1;
  transition: 1s;
}

.socials {
  margin-right: 80px;
}

.navi {
  position: relative;
  margin-bottom: 100px;
}

.social {
  margin-right: 10px;
  width: 32px;
  float: right;
}

.logo-size {
  width: 200px;
}

.navigation-choosen {
  cursor: pointer;
  background-color: #e9c8c1;
  color: white;
}

.navigation:hover {
  cursor: pointer;
  background-color: #e9c8c1;
  color: white;
}

.navigation-active {
  cursor: pointer;
  color: #e9c8c1;
}

.navigation-line {
  border: 1px solid #e9c8c1;
  border-radius: 5px;
}

.footer {
  background-color: #e9c8c1 !important;
}

.App {
  text-align: center;
}

p {
  font-family: Garamond, Baskerville, Baskerville Old Face, Hoefler Text,
    Times New Roman, serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

